import React from 'react'
import { newPost } from '../../apis/rootapi'
import cookie from 'js-cookie'
class Newpost extends React.Component {
  newPost = (e) => {
    e.preventDefault()
    console.log(newPost);
    const title = this.refs.title.value;
    const about = this.refs.about.value;
    const text = this.refs.text.value;
    const tag = this.refs.tag.value;
    if (title.trim() == "") {
      alert("title不能为空")
    } else if (about.trim() == "") {
      alert("about不能为空")
    } else if (text.trim() == "") {
      alert("text不能为空")
    } else if (tag.trim() == "") {
      alert("tag不能为空")
    } else {
      console.log(cookie.get("token"));
      let article = {
        "article": {
          "title": title,
          "token": cookie.get("token"),
          "description": about,
          "body": text,
          "tagList": [tag]
        }
      }
      newPost(article).then((res) => {
        console.log(res);
      })
    }
  }
  render() {
    return (
      <div class="editor-page">
        <div class="container page">
          <div class="row">

            <div class="col-md-10 offset-md-1 col-xs-12">
              <form>
                <fieldset>
                  <fieldset class="form-group">
                    <input type="text" class="form-control form-control-lg" placeholder="Article Title" ref="title" />
                  </fieldset>
                  <fieldset class="form-group">
                    <input type="text" class="form-control" placeholder="What's this article about?" ref="about" />
                  </fieldset>
                  <fieldset class="form-group">
                    <textarea class="form-control" rows="8" placeholder="Write your article (in markdown)" ref="text"></textarea>
                  </fieldset>
                  <fieldset class="form-group">
                    <input type="text" class="form-control" placeholder="Enter tags" ref="tag" /><div class="tag-list"></div>
                  </fieldset>
                  <button class="btn btn-lg pull-xs-right btn-primary" type="button" onClick={this.newPost}>
                    Publish Article
              </button>
                </fieldset>
              </form>
            </div>

          </div>
        </div>
      </div>
    )
  }
}

export default Newpost