import React from 'react';
import { NavLink } from 'react-router-dom'
import store from '../../store/store'

import Pubsub from "pubsub-js"

class Header extends React.Component {
    state = {
        loginok:''
    }
    componentDidMount(){
        Pubsub.subscribe("token",function(msg,data){
            console.log("00000000000000000000");
              console.log(msg,data,"00000000044444")
        })
    }
    render() {
        const { loginok, userdata } = store.getState()
        console.log(userdata);
        const userdatas = userdata == undefined ? {} : userdata 
        console.log(loginok, "store");
        return <div>
            <nav className="navbar navbar-light">
                <div className="container">
                    <a className="navbar-brand" href="index.html">conduit</a>
                    <ul className="nav navbar-nav pull-xs-right">
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/">Home</NavLink>
                        </li>
                        <li class="nav-item" style={{ "display": !loginok ? "none" : "block" }}>
                            <NavLink className="nav-link" to="/newpost">
                                <i class="ion-compose"></i>&nbsp;New Post
                            </NavLink>
                        </li>
                        <li className="nav-item" style={{ "display": loginok ? "none" : "block" }}>
                            <NavLink className="nav-link" to="/login">Sign in</NavLink>
                        </li>
                        <li className="nav-item" style={{ "display": !loginok ? "none" : "block" }}>
                            <NavLink className="nav-link" to="/settings">
                                <i className="ion-gear-a"></i>&nbsp;Settings
                            </NavLink>
                        </li>
                        <li className="nav-item" style={{ "display": loginok ? "none" : "block" }}>
                            <NavLink className="nav-link" to="/registration">Sign up</NavLink>
                        </li>
                        <li className="nav-item" style={{ "display": !loginok ? "none" : "block" }}>
                            <NavLink className="nav-link"
                                to={{ pathname: "/homepage", state: { msg: userdatas.username } }}>
                                {userdatas.username}
                            </NavLink>
                        </li>
                    </ul>
                </div>
            </nav>
        </div>
    }
}

export default Header;
