import axios from 'axios';
import React from 'react';
import { zhuce } from '../../apis/rootapi'
class Registration extends React.Component {
    login = (e) => {
        e.preventDefault()
        let username = this.refs.username.value
        let emil = this.refs.emil.value
        let pwd = this.refs.pwd.value
        let data = {
            user: {
                username: username,
                email: emil,
                password: pwd
            }
        }
        console.log(data);
        // axios({
        //     method:"post",
        //     url:"https://conduit.productionready.io/api/users",
        //     data
        // }).then((res)=>{
        //     console.log(res);
        // })
        console.log(data);
        zhuce(data).then((res) => {
            console.log(res);
            let str = JSON.stringify({ type: "userdata", userdata: res.data.user, loginok: true })
            sessionStorage.setItem("state", str)
            this.props.history.push("/")
        }).catch((err) => {
            console.log(err);
        })
    }
    render() {
        return <div>
            <div className="auth-page">
                <div className="container page">
                    <div className="row">

                        <div className="col-md-6 offset-md-3 col-xs-12">
                            <h1 className="text-xs-center">Sign up</h1>
                            <p className="text-xs-center">
                                <a href="">Have an account?</a>
                            </p>

                            <ul className="error-messages">
                                <li>That email is already taken</li>
                            </ul>

                            <form>
                                <fieldset className="form-group">
                                    <input className="form-control form-control-lg" type="text" placeholder="Your Name" ref="username" />
                                </fieldset>
                                <fieldset className="form-group">
                                    <input className="form-control form-control-lg" type="text" placeholder="Email" ref="emil" />
                                </fieldset>
                                <fieldset className="form-group">
                                    <input className="form-control form-control-lg" type="password" placeholder="Password" ref="pwd" />
                                </fieldset>
                                <button className="btn btn-lg btn-primary pull-xs-right" onClick={this.login}>
                                    Sign up
                                </button>
                            </form>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    }
}

export default Registration;
