import { createStore } from "redux"

const reducer = (state={loginok:false},action)=>{
    let obj = JSON.parse(sessionStorage.getItem("state"))=== null?{}:JSON.parse(sessionStorage.getItem("state"))
    console.log(obj,"session");
    switch (obj.type) {
        case "userdata":
            return {...state,userdata:obj.userdata,loginok:obj.loginok}

        default:
           return {...state}
    }
}
const store = createStore(reducer)
console.log(store.getState());
export default store