import axios from 'axios';
import React from 'react';
import { NavLink } from 'react-router-dom';
import store from '../../../store/store'
import Cookie from 'js-cookie'
import { dz, remdz } from "../../apis/rootapi"

class Home extends React.Component {
    state = {
        datalist: [],
        yourfeed: [],
        globalfeed: [],
        msg: true,
        articlesCount: [],
        pic: 0,
        done:false
    }
    componentDidMount() {

        console.log(Cookie.get("token"), "??__--");
        const data = JSON.parse(sessionStorage.getItem("state"))
        if (Cookie.get("token") && data) {
            console.log("---");
            const username = JSON.parse(sessionStorage.getItem("state")).userdata.username
            this.getdata(`https://conduit.productionready.io/api/articles?author=${username}&limit=5&offset=0`)
                .then((res) => {
                    this.setState({
                        yourfeed: res.data.articles,

                    })
                })
        } else {
            console.log("00");
            Cookie.remove("token")
        }

        this.getdata("https://conduit.productionready.io/api/articles?limit=10")
            .then((res) => {
                console.log(res);
                let list = []
                for (let i = 0; i < (res.data.articlesCount / 10); i++) {
                    list.push(i)
                }
                this.setState({
                    globalfeed: res.data.articles,
                    datalist: res.data.articles,
                    articlesCount: list
                })
            })


    }
    getdata = (username) => {
        return axios.get(username)
    }
    changemsg = () => {
        const { msg, globalfeed, yourfeed } = this.state
        if (msg) {
            this.setState({
                msg: !msg,
                datalist: yourfeed
            })
        } else {
            this.setState({
                msg: !msg,
                datalist: globalfeed
            })
        }

    }
    gonext = (i) => {
        this.setState({
            pic: i
        })
        // axios({
        //     method:"get",
        //     url:"https://conduit.productionready.io/api/articles",
        //     params:{
        //         limit:10,
        //         offset:i
        //     }
        // }).then((res)=>{
        //     console.log(res,"0000????")
        // })
        this.getdata(`https://conduit.productionready.io/api/articles?limit=10&offset=${i}`)
            .then((res) => {
                this.setState({
                    datalist: res.data.articles
                })
            })
    }
    dianzan = (data) => {
        const { datalist } = this.state
        this.setState({
            done:true,
        })
        if (data.favorited) {
            remdz(data).then((res) => {
                console.log(res, "取消");
                data.favorited = false
                data.favoritesCount += -1;
                let index = datalist.indexOf(data)
                datalist[index] = data
                this.setState({
                    datalist: datalist,
                    done:false
                })
            })

        } else {
            dz(data).then((res) => {
                console.log(res, "点赞");
                data.favorited = true
                data.favoritesCount += 1;
                let index = datalist.indexOf(data)
                datalist[index] = data
                this.setState({
                    done:false,
                    datalist: datalist
                })
            })

        }

    }
    render() {
        const { datalist, msg, articlesCount, pic,done } = this.state
        const { loginok } = store.getState()
        return <div>
            <div className="home-page">

                <div className="banner">
                    <div className="container">
                        <h1 className="logo-font">conduit</h1>
                        <p>A place to share your knowledge.</p>
                    </div>
                </div>

                <div className="container page">
                    <div className="row">

                        <div className="col-md-9">
                            <div className="feed-toggle">
                                <ul className="nav nav-pills outline-active">
                                    <li className="nav-item" style={{ "display": !loginok ? "none" : "block" }}>
                                        <a className={msg ? "nav-link" : "nav-link active"} href="javascript:;" onClick={this.changemsg}>Your Feed</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className={!msg ? "nav-link" : "nav-link active"} href="javascript:;" onClick={this.changemsg}>Global Feed</a>
                                    </li>
                                </ul>
                            </div>
                            {
                                datalist.length > 0 ?
                                    datalist.map((item, index) => {
                                        return <div className="article-preview" key={index}>
                                            <div className="article-meta">
                                                <a href="profile.html"><img src={item.author.image} /></a>
                                                <div className="info">
                                                    <NavLink to={{ pathname: "/homepage", state: { msg: item.author.username } }} className="author">{item.author.username}</NavLink>
                                                    <span className="date">January 20th</span>
                                                </div>
                                                <button className="btn btn-outline-primary btn-sm pull-xs-right"
                                                    onClick={() => { this.dianzan(item) }} disabled={done}>
                                                    <i className="ion-heart"></i> {item.favoritesCount}
                                                </button>
                                            </div>
                                            <a href="" className="preview-link">
                                                <h1><NavLink to="/article">{item.title}</NavLink></h1>
                                                <p>{item.description}</p>
                                                <span>Read more...</span>
                                            </a>
                                        </div>

                                    }) : <div>没有发布文章</div>
                            }
                            <ul className="pagination" style={{ "display": !msg ? "none" : "block" }}>
                                {
                                    articlesCount.map((item, index) => {
                                        return <li className={pic == index ? "page-item ng-scope active" : "page-item ng-scope"}
                                            ng-className="active" key={index} onClick={() => { this.gonext(index) }}>
                                            <a className="page-link ng-binding" href>{index + 1}</a>
                                        </li>
                                    })
                                }


                            </ul>
                        </div>

                        <div className="col-md-3">
                            <div className="sidebar">
                                <p>Popular Tags</p>

                                <div className="tag-list">
                                    <a href="" className="tag-pill tag-default">programming</a>
                                    <a href="" className="tag-pill tag-default">javascript</a>
                                    <a href="" className="tag-pill tag-default">emberjs</a>
                                    <a href="" className="tag-pill tag-default">angularjs</a>
                                    <a href="" className="tag-pill tag-default">react</a>
                                    <a href="" className="tag-pill tag-default">mean</a>
                                    <a href="" className="tag-pill tag-default">node</a>
                                    <a href="" className="tag-pill tag-default">rails</a>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>

            </div>
        </div>
    }
}

export default Home;
