import React from 'react';
import {login} from '../../apis/rootapi'
import cookie from 'js-cookie'

import Pubsub from "pubsub-js"
class Login extends React.Component {

    loginrealword = (e) => {
        e.preventDefault()
        const emil = this.refs.emil.value
        const pwd = this.refs.pwd.value
        if(emil.trim()==''){
            alert("账户不能为空")
        }else if (pwd.trim() == '') {
            alert("密码不能为空")
        }else{
            console.log(emil);
            const data = {
                user:{
                    email: emil,
                    password: pwd
                },
                errors: {}
            }
            this.userlogin(data)
        }
    }
    userlogin = (data)=>{
        login(data).then((res)=>{
            console.log(res);
            if (res.status == 200) {
                let str = JSON.stringify({type:"userdata",userdata:res.data.user,loginok:true})
                sessionStorage.setItem("state",str)
                cookie.set("token",res.data.user.token)
                Pubsub.publish("token","？？？？、、、")
                this.props.history.push('/')
                window.history.go(0) 
            }
        }).catch((err)=>{
            alert("账户或密码错误,或者网络错误")
        })
    }
    render() {
        return <div>
            <div className="auth-page">
                <div className="container page">
                    <div className="row">

                        <div className="col-md-6 offset-md-3 col-xs-12">
                            <h1 className="text-xs-center">Sign in</h1>
                            <p className="text-xs-center">
                                <a href="">Have an account?</a>
                            </p>

                            <ul className="error-messages">
                                <li>That email is already taken</li>
                            </ul>

                            <form action="">
                                <fieldset className="form-group">
                                    <input className="form-control form-control-lg" type="text" placeholder="Email" ref="emil"/>
                                </fieldset>
                                <fieldset className="form-group">
                                    <input className="form-control form-control-lg" type="password" placeholder="Password" ref="pwd"/>
                                </fieldset>
                                <button className="btn btn-lg btn-primary pull-xs-right" onClick={(e)=>this.loginrealword(e)}>
                                    Sign up
                                </button>
                            </form>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    }
}

export default Login;
