import React from 'react';
import axios from 'axios'

class HomePage extends React.Component {
    state = {
        auter: {},
        auterlist: []
    }
    componentDidMount() {
        const username = this.props.location.state.msg
        axios({
            method: 'get',
            url:   `https://conduit.productionready.io/api/profiles/${username}`,
        }).then((res) => {
            this.setState({
                auter: res.data.profile
            })
        })
        axios({
            method: 'get',
            url: `https://conduit.productionready.io/api/articles?author=${username}&limit=5&offset=0`,
        }).then((res) => {
            this.setState({
                auterlist: res.data.articles
            })
        })
    }
    render() {
        const { auter, auterlist } = this.state
        console.log(this.props.location.state.msg);
        return <div>
            <div className="profile-page">

                <div className="user-info">
                    <div className="container">
                        <div className="row">

                            <div className="col-xs-12 col-md-10 offset-md-1">
                                <img src={auter.image} className="user-img" />
                                <h4>{auter.username}</h4>
                                <p>
                                    Cofounder @GoThinkster, lived in Aol's HQ for a few months, kinda looks like Peeta from the Hunger Games
                                </p>
                                <button className="btn btn-sm btn-outline-secondary action-btn">
                                    <i className="ion-plus-round"></i>
                                   &nbsp; Follow Eric Simons
                                </button>
                            </div>

                        </div>
                    </div>
                </div>

                <div className="container">
                    <div className="row">

                        <div className="col-xs-12 col-md-10 offset-md-1">
                            <div className="articles-toggle">
                                <ul className="nav nav-pills outline-active">
                                    <li className="nav-item">
                                        <a className="nav-link active" href="">My Articles</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" href="">Favorited Articles</a>
                                    </li>
                                </ul>
                            </div>
                            {
                                auterlist.map((item,index) => {
                                    return <div className="article-preview" key={index}>
                                        <div className="article-meta">
                                            <a href=""><img src={auter.image} /></a>
                                            <div className="info">
                                                <a href="" className="author">{auter.username}</a>
                                                <span className="date">January 20th</span>
                                            </div>
                                            <button className="btn btn-outline-primary btn-sm pull-xs-right">
                                                <i className="ion-heart"></i> {item.favoritesCount}
                                         </button>
                                        </div>
                                        <a href="" className="preview-link">
                                            <h1>{item.title}</h1>
                                            <p>{item.description}</p>
                                            <span>Read more...</span>
                                        </a>
                                    </div>
                                })
                            }



                        </div>

                    </div>
                </div>

            </div>
        </div>
    }
}

export default HomePage;
