import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import store from './store/store'
import { BrowserRouter } from "react-router-dom";
import App from './App';


store.subscribe(() => {
  ReactDOM.render(
    <BrowserRouter>
      <App />
    </BrowserRouter>
    ,

    document.getElementById('root')
  )
})


ReactDOM.render(
  <BrowserRouter>
    <App />
  </BrowserRouter>
  ,

  document.getElementById('root')
)


